import React, { useEffect, useState } from "react"
import { Link } from "gatsby"

import { Container } from "./styles"

export default function RBaseHeader() {
  const [hasPass, setHasPass] = useState(false)
  const [shouldBeSolid, setShouldBeSolid] = useState(false)

  useEffect(() => {
    function cb() {
      if (typeof window !== "undefined") {
        if (window.scrollY >= 100) {
          setHasPass(true)
        }
        if (window.scrollY <= 99) {
          setHasPass(false)
        }
      }
    }
    cb()

    if (typeof window !== "undefined") {
      window.addEventListener("scroll", cb)
      const pathname = window.location.pathname

      setShouldBeSolid(pathname.includes("about") || pathname.includes("blog"))
    }

    return () =>
      typeof window !== "undefined" && window.removeEventListener("scroll", cb)
  }, [])

  return (
    <Container className={shouldBeSolid ? "solid" : hasPass ? "solid" : ""}>
      <nav>
        <Link to="/">Início</Link>
        <Link to="/about">Sobre</Link>
        <Link to="/#segmentos">Segmentos</Link>
        <Link to="/#contato">Contato</Link>
        <Link to="/blog">Blog</Link>
      </nav>
    </Container>
  )
}
